@use 'sass:math';
@use '../../_config.scss' as *;

.home-about{
    @include imageRatio(910,378);
    &__grid{
        display: grid;
        grid-template-columns: repeat(12,1fr);
        gap: 30px;
    }
    &__info-container{
        grid-column: 2/6;
    }
    &__image-container{
        grid-column: 7/-1;
    }

    @media only screen and (max-width: 991.98px) {
        @include imageRatio(910,500);
        &__info-container{
            grid-column: 1/-1;
            margin-bottom: 42px;
        }
        &__image-container{
            grid-column: 1/-1;
        }
    }

    @media only screen and (max-width: 767.98px) {
       
        
    }
}